import { Component, OnInit } from '@angular/core';
import { EventsService } from '../events.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-eevents',
  templateUrl: './admin-eevents.component.html',
  styleUrls: ['./admin-eevents.component.css']
})
export class AdminEeventsComponent implements OnInit {

  events;
  constructor(public eventsService: EventsService,
    private route: Router) {
    eventsService.getEvents().subscribe(data => (this.events = data));
  }

  deleteEvent(id){
    this.eventsService.remove(id).subscribe(data=> {
      if (data) {
        this.eventsService.getEvents().subscribe(d => this.events = d);  //to make auto refresh after delete 
      }
    });
    console.log("success");   
  }
  goEditEvent(id)
    {  this.route.navigate(["editevent",id]);
    }
  ngOnInit() {
  }

}
