import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AppComponent } from "./app.component";
import { EventsComponent } from "./events/events.component";
import { LoginComponent } from "./login/login.component";
import { ProfileComponent } from "./profile/profile.component";
import { AddEventComponent } from './add-event/add-event.component';

import { HttpClientModule } from "@angular/common/http";

import { RouterModule, Route } from "@angular/router";
import { DetailsComponent } from './details/details.component';
import { AdminEeventsComponent } from './admin-eevents/admin-eevents.component';
import { EditEeventComponent } from './edit-eevent/edit-eevent.component';

const route: Route[] = [
  {
    path: "login",
    component: LoginComponent
  },
  {
    path: "profile",
    component: ProfileComponent
  },
  {
    path: "events",
    component: EventsComponent
  },
  {
    path: "addEvent",
    component: AddEventComponent
  },
  {
    path: "detailevent/:id",
    component: DetailsComponent
  },
  {
    path: "editevent/:id",
    component: EditEeventComponent
  },
  
  {
    path:"adminevnt",
    component:AdminEeventsComponent
  },
  {
    path: "",
    redirectTo: "/events",
    pathMatch: "prefix"
  }
];

@NgModule({
  declarations: [
    AppComponent,
    EventsComponent,
    LoginComponent,
    ProfileComponent,
    AddEventComponent,
    DetailsComponent,
    AdminEeventsComponent,
    EditEeventComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot(route)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}
