import { Component, OnInit } from '@angular/core';
import { EventsService } from '../events.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, Validators, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-edit-eevent',
  templateUrl: './edit-eevent.component.html',
  styleUrls: ['./edit-eevent.component.css']
})
export class EditEeventComponent implements OnInit {

  
  event;

  title = new FormControl("Angular", [Validators.required]);
  date = new FormControl("", [Validators.required]);
  time = new FormControl("01:22", [Validators.required]);
  price = new FormControl("555", [Validators.required, Validators.maxLength(5)]);
  country = new FormControl("Palestine", [Validators.required]);
  city = new FormControl("Khanyounis", [Validators.required]);
  address = new FormControl("Banisohila", [Validators.required]);

  editEventForm = new FormGroup({
    title: this.title,
    date: this.date,
    time: this.title,
    price: this.price,
    country: this.country,
    city: this.city,
    address: this.address
  });


  constructor(
    public eventsService: EventsService,
    public activatedRoute: ActivatedRoute,
    public router: Router
    ) {
      // console.log(this.activatedRoute.snapshot.params["id"]);

      this.eventsService
      .getEventsId(+this.activatedRoute.snapshot.params["id"])
      .subscribe(data => {
        this.event = data;
      this.title.setValue(this.event.title);
      this.country.setValue(this.event.location.country);
      this.city.setValue(this.event.location.city);
      this.address.setValue(this.event.address);});


  }

    editEvent(){
      this.eventsService.updatePut(this.editEventForm.value).subscribe(data => {
        if (data) {
          this.router.navigate(["/events"]);
        }
      });
    }  
  ngOnInit() {
  }

}
